<template>
  <div class="hello">
    <h3>儿童视频管理后台</h3>
    <div>
      <div id="ctr">
        <a>Add</a>
        <a @click="remove()">Remove</a>
        <a @click="reload()">重新加载</a>
      </div>
      <div id="content">
        <table>
          <tr v-for="(item, i) in items" :key="item.id">
            <td>#{{ i }}</td>
            <td>
              <a target="_blank" :href="item.url">{{ item.title }}</a>
            </td>
            <td><img :src="item.thumb" /></td>
            <td><input type="checkbox" v-model="item.checked" /></td>
            <td><input type="button" value="Play" @click="play(item)" /></td>
          </tr>
        </table>
      </div>
    </div>
    <div id="add">
      <table>
        <tr>
          <td>ID</td>
          <td><input type="text" v-model="curItem.id" /></td>
        </tr>
        <tr>
          <td>URL</td>
          <td><input type="text" v-model="curItem.url" /></td>
        </tr>
        <tr>
          <td colspan="2"><a @click="addOrUpdate(curItem)">Submit</a></td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data: () => {
    return {
      items: [],
      curItem: {
        id: 0,
        url:
          "https://upos-sz-mirrorkodo.bilivideo.com/upgcxcode/30/93/272399330/272399330-1-208.mp4?e=ig8euxZM2rNcNbeghwdVhoMHhbdVhwdEto8g5X10ugNcXBMvNC8xNbLEkF6MuwLStj8fqJ0EkX1ftx7Sqr_aio8_&uipk=5&nbs=1&deadline=1616161160&gen=playurl&os=kodobv&oi=2071795667&trid=1ddbc04303f6456e90ce19ebe984245cT&platform=html5&upsig=09f90fb64437a28dbdb853a18ee72154&uparams=e,uipk,nbs,deadline,gen,os,oi,trid,platform&mid=0&orderid=0,1&logo=80000000",
      },
    };
  },
  mounted: function () {
    this.getDatas();
  },
  props: {
    msg: String,
  },
  methods: {
    base64(item) {
      let n = "data:image/png;base64," + item.thumb;
      console.log(n);
      return n;
    },
    play(item) {
      fetch("/api/play", {
        method: "post",
        body: JSON.stringify(item),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    addOrUpdate(item) {
      fetch("/api/addOrUpdate", {
        method: "post",
        body: JSON.stringify(item),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    remove() {
      fetch(
        "/api/delete?ids=" +
          this.items
            .filter((e) => e.checked)
            .map((e) => e.id)
            .join(",")
      ).then(() => {
        var that = this;
        for (var i = that.items.length - 1; i >= 0; i--) {
          var item = that.items[i];
          if (item.checked) {
            that.items.splice(i, 1);
          }
        }
      });
    },
    getDatas() {
      fetch("/api/list", {
        method: "GET",
        mode: "cors",
        credentials: "include",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },

    reload() {
      fetch("/api/reloadplaylist", {
        method: "GET",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
  },
};
</script>

